.BadgesList {
  width: 100%;
  padding: 0 1rem;
}

.BadgesList__item {
  display: grid;
  grid-template-columns: 3fr 7fr;
  grid-template-areas: "avatar text";
  margin: 1rem 0;
  box-shadow: 0px 0px 17px 6px rgba(181, 181, 181, 1);
  padding: 1.5rem 1rem;
  -webkit-align-items: center;
          align-items: center;
}

.BadgesList__avatar {
  grid-area: avatar;
}

.BadgesList__avatar img {
  border-radius: 50%;
}

.BadgesList__text {
  grid-area: text;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.BadgesList__name {
  font-size: 1.2rem;
  margin-bottom: 0;
}

.BadgesList__twitter img {
  width: 35px;
}

.BadgesList__twitter a {
  text-decoration: none;
}

.BadgesList_job {
  margin-bottom: 0;
}

@media screen and (max-width: 480px) {
  .BadgesList__item {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas:
      "avatar"
      "text";
    text-align: center;
  }
}

.PageLoading {
